/* Material Icons Font (for MD theme) */
@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(/static/media/MaterialIcons-Regular.e79bfd88.eot); /* For IE6-8 */
  src: local('Material Icons'),
       local('MaterialIcons-Regular'),
       url(/static/media/MaterialIcons-Regular.570eb838.woff2) format('woff2'),
       url(/static/media/MaterialIcons-Regular.012cf6a1.woff) format('woff'),
       url(/static/media/MaterialIcons-Regular.a37b0c01.ttf) format('truetype');
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  -webkit-font-feature-settings: 'liga';
          font-feature-settings: 'liga';
}

/* Framework7 Icons Font (for iOS theme) */
@font-face {
  font-family: 'Framework7 Icons';
  font-style: normal;
  font-weight: 400;
  src: url(/static/media/Framework7Icons-Regular.28db8299.eot);
  src: url(/static/media/Framework7Icons-Regular.19158ff0.woff2) format("woff2"),
       url(/static/media/Framework7Icons-Regular.5fce7c12.woff) format("woff"),
       url(/static/media/Framework7Icons-Regular.11137101.ttf) format("truetype");
}

.f7-icons {
  font-family: 'Framework7 Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 25px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-feature-settings: "liga";
  font-feature-settings: "liga";
  text-align: center;
}

/* Your app styles here */
.navbar {
    background-color: #009688 !important;
    background: #009688 !important;
    color: #fff;
}
.f7-icons[data-icon]:before {
    content: attr(data-icon);
    color: inherit;
}
.contact__img {border-radius: 50em}


.fz-11 {font-size: 11px;}
.fz-12 {font-size: 12px;}
.fz-13 {font-size: 13px;}

.if7-email {
    position: relative;
    left: -6px;
}
.mt-0 {margin-top: 0 !important;}
.mb-0 {margin-bottom: 0 !important;}
.m-0 {margin: 0 !important;}
.color-muted {color: #999;}

.searchbar input[type="text"], .theme-dark .searchbar input[type="search"] {
    color: #009688;
    background-color: #171717;
}
.navbar a {
    color: #fff;
}
.tabbar a, .tabbar-labels a {
    color: #333;
}
.theme-dark a.link {color: #ddd;}
.text-center {text-align: center !important;}
a {
    cursor: pointer;
    text-decoration: none;
    color: #092321;
}
hr {
    color: #555; /*для IE */
    background-color:#555; /*для Firefox, Opera, Safari*/
    border:0px none;
    height:1px; /* высота 1px IE, Firefox, Opera, Safari */
    clear:both; /* для очистки, если тег идет после float елемента */
}
